import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HeroDetailsComponent } from 'src/app/hero/hero-details/hero-details.component';
import { DashboardComponent } from 'src/app/hero/dashboard/dashboard.component';
import { HeroesComponent } from 'src/app/hero/heroes/heroes.component';
import { HeroService } from 'src/app/hero/services/hero.service';
import { InMemoryDataService } from 'src/app/hero/services/in-memory-data.service';
import { HttpClientModule, HttpHeaders } from '@angular/common/http';
import { Headers, HttpModule } from '@angular/http';
import { HeroSearchService } from 'src/app/hero/services/hero-search.service';
import { CommonModule } from '@angular/common';
import { HeroComponent } from 'src/app/hero/hero.component';
import { MaterialModule } from 'src/app/shared/material.module';

const routes: Routes = [
  { path: '', redirectTo: 'services/dashboard', pathMatch: 'full' },
  { path: 'services/dashboard', component: DashboardComponent },
  { path: 'detail/:id', component: HeroDetailsComponent },
  { path: 'services/heroes', component: HeroesComponent }
];

@NgModule({
  imports: [HttpModule,HttpClientModule,CommonModule,MaterialModule,RouterModule.forRoot([
   
    
   ])],
   providers: [HeroService,InMemoryDataService,HeroSearchService],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingModule {
}
